import React from "react"
import Sun from "../images/sun.png"
import Moon from "../images/moon.png"

function getDefaultTheme() {
  if (typeof window != "undefined") {
    const savedTheme = window.localStorage.getItem("theme")
    return savedTheme ? savedTheme : "light"
  }

}

export default function DarkMode() {
  const [isDark, setIsDark] = React.useState(getDefaultTheme())

  React.useEffect(() => {
    if (isDark === "dark") {
      document.body.classList.add("dark")
    } else {
      document.body.classList.remove("dark")
    }
    window.localStorage.setItem("theme", isDark)
  }, [isDark])

  return (
    <div className="global-toggle-switch">
      <span onClick={() => setIsDark(isDark === "dark" ? "light" : "dark")}>
        {isDark === "dark" ? (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <circle cx="12" cy="12" r="5" />
            <line x1="12" y1="1" x2="12" y2="3" />
            <line x1="12" y1="21" x2="12" y2="23" />
            <line x1="4.22" y1="4.22" x2="5.64" y2="5.64" />
            <line x1="18.36" y1="18.36" x2="19.78" y2="19.78" />
            <line x1="1" y1="12" x2="3" y2="12" />
            <line x1="21" y1="12" x2="23" y2="12" />
            <line x1="4.22" y1="19.78" x2="5.64" y2="18.36" />
            <line x1="18.36" y1="5.64" x2="19.78" y2="4.22" />
          </svg>

        ) : (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"

            stroke-linecap="round"
            stroke-linejoin="round"
            style={{ transform: "rotate(90deg)" }}
          >
            <mask id="mask">
              <rect x="0" y="0" width="100%" height="100%" fill="#FFF" />
              <circle cx="12" cy="4" r="9" fill="black" />
            </mask>
            <circle fill="black" cx="12" cy="12" r="9" mask="url(#mask)" />
          </svg>
        )}
      </span>
    </div>
  )
}